.post-container {
  max-width: 420px;
  border: 0.5px solid darkgray;
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .user-info {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
  }
  
  .post-content {
    padding-left: 20px;
    margin-bottom: 10px;
  }

  .post-footer {
    /* border: 0.5px solid red; */
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
  }

  .post-icon {
    padding: 10px;
    border-radius: 25px;
  }
  .post-icon:hover{
    cursor: pointer;
    color: white;
    background-color:var(--theme-color);
  }

  .post-icon-div {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
  }

  .rebarked-main {
    margin: 10px;
    border: 0.5px solid darkgray;
    border-radius: 18px;
  }

  .post-w-comments-container {
    border: 0.5px solid darkgray;
    max-width: 420px;
    margin-bottom: 10px;
  }

  .comment-container {
    /* margin-left: 15px; */
    border-top: 0.5px solid darkgray;
  }

  .comment-main {
    margin-left: 30px;
  }
  

  .link-text-content:link {
    text-decoration: none;
    color:black;
    
  }
  
  .link-text-content:visited {
    text-decoration: none;
    color:black;
  
  }
  
  .link-text-content:hover {
    text-decoration: none;
    
  }
  
  .link-text-content:active {
    text-decoration: none;
    
  }